





















































































































import { Component, Mixins, Prop, Watch } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import { trim, filter, debounce } from 'lodash'
import getRepository from '@/services'
import countryJson from '@/utils/country/index.json'
interface IListCountry {
  name: string
  dialCode: string
  isoCode: string
  flag: string
}
@Component({ components: {} })
export default class PopupFilterCrowdsale extends Mixins(PopupMixin) {
  form: any = {
    round: '',
    country: '',
    byWallet: '',
    byToken: '',
    transactionDateStart: '',
    transactionDateEnd: '',
    transactionAmountStart: '',
    transactionAmountEnd: ''
  }
  listCountry: IListCountry[] = countryJson
  pickerOptions = {
    disabledDate(time: any) {
      return time.getTime() < Date.now()
    }
  }
  optionByToken: any = [
    {
      id: '1',
      label: 'BTC'
    },
    {
      id: '2',
      label: 'LYNK'
    },
    {
      id: '3',
      label: 'ETH'
    }
  ]
  optionByWallet: any = [
    {
      id: '1',
      label: 'Meta Mask'
    },
    {
      id: '2',
      label: 'Wallet Binance'
    },
    {
      id: '3',
      label: 'Wallet ETH'
    }
  ]
  optionByRound: any = [
    {
      id: '1',
      label: `${this.$t('crowdsale.popup-filter.round')} 1`
    },
    {
      id: '2',
      label: `${this.$t('crowdsale.popup-filter.round')} 2`
    },
    {
      id: '3',
      label: `${this.$t('crowdsale.popup-filter.round')} 3`
    },
    {
      id: '4',
      label: `${this.$t('crowdsale.popup-filter.round')} 4`
    }
  ]
  handleReset(): void {
    this.form = {
      round: '',
      country: '',
      byWallet: '',
      byToken: '',
      transactionDateStart: '',
      transactionDateEnd: '',
      transactionAmountStart: '',
      transactionAmountEnd: ''
    }
    if (this.$refs.filterCrowdsale) {
      //@ts-ignore
      this.$refs.filterCrowdsale.clearValidate()
    }
  }
  //validate
  rules: any = {
    round: [
      {
        required: true,
        message: this.$t('crowdsale.popup-filter.required'),
        trigger: 'change'
      }
    ],
    country: [
      {
        required: true,
        message: this.$t('crowdsale.popup-filter.required'),
        trigger: 'change'
      }
    ],
    byWallet: [
      {
        required: true,
        message: this.$t('crowdsale.popup-filter.required'),
        trigger: 'change'
      }
    ],
    byToken: [
      {
        required: true,
        message: this.$t('crowdsale.popup-filter.required'),
        trigger: 'change'
      }
    ],
    transactionDateStart: [
      {
        required: true,
        message: this.$t('crowdsale.popup-filter.required'),
        trigger: 'change'
      }
    ],
    transactionDateEnd: [
      {
        required: true,
        message: this.$t('crowdsale.popup-filter.required'),
        trigger: 'change'
      }
    ],
    transactionAmountStart: [
      {
        required: true,
        message: this.$t('crowdsale.popup-filter.required'),
        trigger: 'change'
      }
    ],
    transactionAmountEnd: [
      {
        required: true,
        message: this.$t('crowdsale.popup-filter.required'),
        trigger: 'change'
      }
    ]
  }
  validateForm(): boolean {
    let filterCrowdsale: any = this.$refs.filterCrowdsale
    let valid = false
    filterCrowdsale.validate((_valid: boolean) => {
      valid = _valid
    })
    return valid
  }
  async handleSubmit(): Promise<void> {
    const valid = this.validateForm()
    if (valid) {
      console.log(1)
    }
  }
  async handleOpen(): Promise<void> {
    await this.handleReset()
  }
  async handleClose(): Promise<void> {
    await this.handleReset()
  }
  numberFormat(event: FocusEvent): void {
    const _event: any = event
    let fnumber = _event.target.value
    if (fnumber.length > 0) {
      fnumber = fnumber.replaceAll(',', '')
      fnumber = this.$options.filters?.numberWithCommas(fnumber)
      _event.target.value = fnumber
    }
  }
  remoteCountry(query: string): void {
    if (query) {
      const currentCountry = filter(
        this.listCountry,
        country => trim(country.isoCode).toUpperCase().includes(query.toUpperCase()) || trim(country.name).toUpperCase().includes(query.toUpperCase())
      )
      if (currentCountry.length > 0) {
        this.listCountry = currentCountry
      }
    } else if (!query) {
      this.listCountry = countryJson
    }
  }
  created(): void {
    const currentCountry = filter(this.listCountry, country => country.isoCode === 'VN')[0]
    this.form.country = currentCountry.name
  }
}
